.input-tag {
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 10px 15px;
    margin: 5px;
    display: inline-block;
    cursor: pointer;
    position: sticky;
    background-color: #f9f9f9;
    transition: background-color 0.3s, border-color 0.3s;
}

.input-tag:hover {
    background-color: #e6e6e6;
    border-color: #aaa;
}

.input-tag:focus {
    outline: none;
    border-color: #007BFF;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.container .input-tag {
    margin: 5px;
}